<template>
	<div class="my container">
		<header>
			<div class="user" v-if="loginStatus">
				<img :src="userInfo.imgUrl" alt=""/>
				<span>{{userInfo.nickName}}</span>
			</div>
			<div class="login" @click="goLogin" v-else>登录/注册</div>	
		</header>
		<section>
			<ul>	
				<li><i class="iconfont icon-pintuan"></i>我的拼团</li>	
				<li><i class="iconfont icon-shoucang"></i>我的收藏</li>
				<li><i class="iconfont icon-youhuijuan-01"></i>优惠劵</li>		
				<li @click="goPath"><i class="iconfont icon-shouhuodizhiguanli"></i>地址管理</li>		
				<li v-if="loginStatus" @click="loginOut"><i class="iconfont icon-tuichu"></i>退出登录</li>
				
			</ul>
		</section>
		<Tabbar></Tabbar>
	</div>
</template>

<script>
	import {mapState,mapMutations} from 'vuex'
	import Tabbar from '@/components/common/Tabbar.vue'
	export default{
		name:"My",
		components:{
			Tabbar
		},
		computed:{
			...mapState({
				loginStatus:state=>state.user.loginStatus,
				userInfo:state=>state.user.userInfo
			})
		},
		methods:{
			...mapMutations(['loginOut']),
			goLogin(){
				this.$router.push('/login')
			},
			// 进入地址管理
			goPath(){
				this.$router.push('/address')
			}
		}
	}
</script>

