	.user{
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
	.user img{
		width: 1.76rem;
		height:1.76rem;
		border-radius: 50%;
	}
	.user span{
		padding:0.533333rem 0;
		font-size:0.48rem;
		color: #fff;
	}
	.login{
		padding:0.16rem 0.4rem;
		font-size:0.426666rem;
		background-color: #f6ab32;
		color: #fff;
		border-radius: 6px;
	}
	header{
		display: flex;
		justify-content: center;
